import React, { forwardRef } from 'react'
import { EuiButtonProps } from '@elastic/eui/src/components/button/button'
import { EuiButton } from '@elastic/eui'

const Button = forwardRef<typeof EuiButton, EuiButtonProps>((props, ref) => (
  // @ts-ignore
  <EuiButton buttonRef={ref} {...props}>
    {props.children}
  </EuiButton>
))

Button.displayName = `forwardRef(EuiButton)`

export default Button
